import React, { Component } from 'react';
import * as ons from 'onsenui'
import * as Ons from 'react-onsenui'
import QrReader from 'react-qr-reader'
import ActivityListPage from './ActivityListPage';

class LocationPage extends Component {
  constructor(props) {
    super(props);
    console.log(this.props);

    this.state = {
      result: null,
      displayCamera: false
    }

    this.renderToolbar = this.renderToolbar.bind(this);
    this.getMap = this.getMap.bind(this);
    this.handleScan = this.handleScan.bind(this);
    this.handleError = this.handleError.bind(this);
    this.displayCamera = this.displayCamera.bind(this);
    this.pushPage = this.pushPage.bind(this);
    this.resetState = this.resetState.bind(this);
  }

  renderToolbar() {
    return (
      <Ons.Toolbar>
        <div className="left">
          <Ons.BackButton></Ons.BackButton>
        </div>
        <div className="center">
          Tourism Moose Jaw App
        </div>
        <div className='right'>
          <Ons.ToolbarButton>
            <Ons.Icon icon='ion-navicon, material:md-menu'></Ons.Icon>
          </Ons.ToolbarButton>
        </div>
      </Ons.Toolbar>
    );
  }

  handleScan = data => {
    if (data) {
      console.log(data);
      const location = this.props.locations[this.props.id];
      if (data === location.title) {
        this.setState({
          result: data
        });
        this.pushPage(this.props.navigator);
      } else {
        this.setState({
          displayCamera: false
        });
        ons.notification.alert(`Sorry, looks like you are not in ${location.title}`);
      }
    }
  }

  handleError = err => {
    console.error(err)
  }

  displayCamera() {
    this.setState({
      displayCamera: true
    })
  }

  getMap(locationName) {
    window.open(`https://maps.google.com/?q=${locationName.replace(' ', '+')}`);
  }

  resetState() {
    this.setState({
      result: null,
      displayCamera: false
    });
  }

  pushPage(navigator) {
    navigator.pushPage({
      component: ActivityListPage,
      props: {
        result: this.state.result,
        resetState: this.resetState
      },
    });
  }

  render() {
    console.log(this.props);
    const location = this.props.locations[this.props.id];

    return (
      <Ons.Page renderToolbar={this.renderToolbar}>
        <Ons.Card>
          <div className="title right">
            {location.title}
          </div>
          <img src={location.image} alt={location.title} style={{width: '100%'}} />
          <div className="content">
            {location.description}
          </div>
          { this.state.displayCamera && !this.state.result &&
          <QrReader
            delay={1000}
            onError={this.handleError}
            onScan={this.handleScan}
            style={{ width: '100%' }}
          />
          }
          <section style={{margin: '16px'}}>
            <div style={{margin: '6px'}}>
              <Ons.Button onClick={this.displayCamera} modifier='large'>I am here</Ons.Button>
            </div>
            <div style={{margin: '6px'}}>
              <Ons.Button onClick={() => this.getMap(location.title)} modifier='large outline'>Get directions</Ons.Button>
            </div>
          </section>
        </Ons.Card>
      </Ons.Page>
    );
  }
}

export default LocationPage;