import React, { Component } from 'react';
import * as Ons from 'react-onsenui';
import ActivityPage from './ActivityPage';

class ActivityListPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      activities: [
        {
          activityName: 'Take a selfie',
          activityDescription: 'You need to take a selfie to prove you have been here',
          isDone: false,
          activityID: 0
        }
      ],
    }

    this.renderToolbar = this.renderToolbar.bind(this);
    this.renderRow = this.renderRow.bind(this);
    this.setDone = this.setDone.bind(this);
  }

  renderToolbar() {
    return (
      <Ons.Toolbar>
        <div className="left">
          <Ons.BackButton></Ons.BackButton>
        </div>
        <div className="center">
          Tourism Moose Jaw App
        </div>
        <div className='right'>
          <Ons.ToolbarButton>
            <Ons.Icon icon='ion-navicon, material:md-menu'></Ons.Icon>
          </Ons.ToolbarButton>
        </div>
      </Ons.Toolbar>
    );
  }

  componentWillUnmount() {
    this.props.resetState();
  }

  setDone(id) {
    const newActivities = this.state.activities.map((row) => {
      if (row.activityID === id) row.isDone = true;
      return row;
    });

    console.log(newActivities);
    
    this.setState({
      activities: newActivities
    });
  }

  pushPage(navigator, id) {
    navigator.pushPage({
      component: ActivityPage,
      props: {
        activity: this.state.activities,
        id: id,
        setDone: this.setDone,
        result: this.props.result
      }
    });
  }

  renderRow(row, index) {
    return (
      <Ons.ListItem
        key={index}
        modifier='longdivider'
        onClick={() => {
          /* actions.selectLocation(id); */
          this.pushPage(this.props.navigator, index);
        }}
        tappable
      >
        <div className = 'left'>
          {index + 1}
        </div>
        <div className='center'>
            {row.activityName}
        </div>
        <div className='right'>
          {row.isDone && <Ons.Icon style={{color: 'green'}} icon="md-check-circle" />}
        </div>
      </Ons.ListItem>
    );
  }

  render() {
    return (
      <Ons.Page renderToolbar={this.renderToolbar}>
        <Ons.List
          dataSource={this.state.activities}
          renderRow={this.renderRow}
          renderHeader={() => <Ons.ListHeader>Activities - {this.props.result}</Ons.ListHeader>} 
        />
      </Ons.Page>
    );
  }
}

export default ActivityListPage;